body{
  background-color: blue;
}
table{
  background-color: white;
  border:1px solid;
  border-radius: 5px;
  width:70%;
  height:70%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
}

table tr{
  font-size: 18px;
  width:100%;
  left:0%;
  margin-left:20px;
}
p{
  margin-left: 20px;
  margin-top: 0px;
  font-size: 20px;
  margin-bottom: 5px;
}
h1{
  margin-left: 20px;
  margin-top: 20px;
 
}
h3{
  margin-left: 20px;
  margin-top: 20px;
  margin-bottom: 10px;
  color:rgb(174, 167, 167);
}
input,select{
  margin-left: 20px;
  width:80%;
  height: 25px;
}
input::placeholder{
  color:rgb(174, 167, 167);
}

button{
  margin:20px;
  width:80%;
  height:30px ;
  color:white;
  background-color: blue;
  font-size: 15px;
}